html, body, div#react-container {
  height: 100%;
  min-height: 100%; }

h1 {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  background: white;
  background: linear-gradient(to bottom, white 0%, #e5e5e5 100%); }

input {
  outline: none; }

div[id="user-login"] {
  padding: 10px;
  margin: 10px;
  width: 100%;
  list-style-type: none;
  display: inline-grid;
  position: relative;
  background-color: white;
  font-family: sans-serif;
  font-size: 24px; }
  @media screen and (min-width: 700px) {
    div[id="user-login"] {
      flex-basis: calc(50% - 2em - 4px); } }
  @media screen and (min-width: 1100px) {
    div[id="user-login"] {
      flex-basis: calc(33.33333% - 2em - 4px); } }
  @media screen and (min-width: 1600px) {
    div[id="user-login"] {
      flex-basis: calc(25% - 2em - 4px); } }
  @media screen and (min-width: 2000px) {
    div[id="user-login"] {
      flex-basis: calc(16.66666% - 2em - 4px); } }
  div[id="user-login"] h1 {
    font-family: Verdana, sans-serif;
    font-size: 2em;
    color: cornflowerblue;
    text-align: left;
    padding: .5em;
    flex-basis: calc(100% - 1em); }
  div[id="user-login"] form {
    position: relative;
    width: 100%;
    display: grid;
    background-color: #ffffff; }
    div[id="user-login"] form > :first-child {
      flex-grow: 0; }
    div[id="user-login"] form input, div[id="user-login"] form button {
      background-color: lightblue;
      color: black;
      border: cornflowerblue;
      padding: 6px 17px;
      font-size: 17px;
      border-style: ridge;
      border-radius: 30px;
      max-width: fit-content; }
    div[id="user-login"] form input[type="email"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="user-login"] form input[type="password"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="user-login"] form input[type="name"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="user-login"] form input[type="text"] {
      background-color: white;
      padding: 0;
      height: +2em;
      width: 250px;
      border-style: double; }
    div[id="user-login"] form input[type="submit"] {
      background-color: cornflowerblue;
      color: whitesmoke;
      border: black;
      margin: .5em;
      padding: .25em;
      font-size: 1.2em;
      height: +2em;
      width: 300px;
      border-style: ridge;
      border-radius: 1px;
      display: inline; }
    div[id="user-login"] form span[id="greenText"] {
      color: green;
      font-weight: bold; }
  div[id="user-login"] > div:last-child {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: .5em; }
  div[id="user-login"] div.color {
    height: 100px;
    flex-basis: 100%; }
  div[id="user-login"] div.time-ago {
    position: absolute;
    bottom: .5em;
    right: .5em;
    font-family: monospace;
    color: #7f7f7f; }
