html, body, div#react-container {
  height: 100%;
  min-height: 100%; }

h1 {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  background: white;
  background: linear-gradient(to bottom, white 0%, #e5e5e5 100%); }

input {
  outline: none; }

div[id="add-user"] {
  height: 320px;
  border-bottom-color: #156e73;
  border-right-color: #156e73;
  margin: 1em;
  padding: 0;
  display: inline-table;
  flex-wrap: wrap;
  position: relative;
  background-color: white; }
  @media screen and (min-width: 700px) {
    div[id="add-user"] {
      flex-basis: calc(50% - 2em - 4px); } }
  @media screen and (min-width: 1100px) {
    div[id="add-user"] {
      flex-basis: calc(33.33333% - 2em - 4px); } }
  @media screen and (min-width: 1600px) {
    div[id="add-user"] {
      flex-basis: calc(25% - 2em - 4px); } }
  @media screen and (min-width: 2000px) {
    div[id="add-user"] {
      flex-basis: calc(16.66666% - 2em - 4px); } }
  div[id="add-user"] form {
    position: relative;
    width: 55%;
    display: grid;
    background-color: #ffffff; }
    div[id="add-user"] form > :first-child {
      flex-grow: 0; }
    div[id="add-user"] form input, div[id="add-user"] form button {
      background-color: lightblue;
      color: black;
      border: cornflowerblue;
      padding: 6px 17px;
      font-size: 17px;
      border-style: ridge;
      border-radius: 41px;
      max-width: fit-content; }
    div[id="add-user"] form span[id="greenText"] {
      color: green;
      font-weight: bold; }
    div[id="add-user"] form input[type="email"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="add-user"] form input[type="password"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="add-user"] form input[type="name"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="add-user"] form input[type="number"] {
      margin: 8px;
      display: block;
      border: 1px solid #bbb;
      color: #515151;
      outline: 0;
      padding: 6px;
      font-size: 1.1em;
      width: fit-content;
      border-radius: 4px;
      background: url(data:image/png;base64,) no-repeat 10px 8px;
      background-size: 16px 16px;
      padding-left: 32px; }
    div[id="add-user"] form input[type="text"] {
      background-color: white;
      padding: 0;
      height: +2em;
      width: 250px;
      border-style: double; }
