html, body, div#react-container {
  height: 100%;
  min-height: 100%; }

h1 {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  background: white;
  background: linear-gradient(to bottom, white 0%, #e5e5e5 100%); }

input {
  outline: none; }

section.tournament {
  box-shadow: 3px 3px 8px 1px #b2b2b2;
  width: 22em;
  border: 2px solid #6fdfe5;
  border-bottom-color: #156e73;
  border-right-color: #156e73;
  margin: .25em;
  padding: 0;
  display: inline-table;
  flex-wrap: wrap;
  position: relative; }
  @media screen and (min-width: 700px) {
    section.tournament {
      flex-basis: calc(50% - 2em - 4px); } }
  @media screen and (min-width: 1100px) {
    section.tournament {
      flex-basis: calc(33.33333% - 2em - 4px); } }
  @media screen and (min-width: 1600px) {
    section.tournament {
      flex-basis: calc(25% - 2em - 4px); } }
  @media screen and (min-width: 2000px) {
    section.tournament {
      flex-basis: calc(16.66666% - 2em - 4px); } }
  section.tournament h1 {
    font-family: Verdana, sans-serif;
    text-align: center;
    padding: .5em;
    flex-basis: calc(100% - 1em); }
  section.tournament button {
    position: absolute;
    top: .5em;
    right: .5em;
    flex-basis: calc(12% - 1em);
    text-align: right;
    background: white;
    background: linear-gradient(to bottom, white 0%, #e5e5e5 100%);
    padding: .75em;
    border-radius: .5px; }
  section.tournament > div:last-child {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: .5em; }
  section.tournament div.color {
    height: 100px;
    flex-basis: 100%; }
  section.tournament div.time-ago {
    position: absolute;
    bottom: .5em;
    right: .5em;
    font-family: monospace;
    color: #7f7f7f; }
