html, body, div#react-container {
  height: 100%;
  min-height: 100%; }

h1 {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  background: white;
  background: linear-gradient(to bottom, white 0%, #e5e5e5 100%); }

input {
  outline: none; }

div[id="videos-list"] {
  width: 960px;
  margin: 0px auto;
  text-align: left; }

div.tournamentx-list {
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  overflow: scroll; }
