html, body, div#react-container {
  height: 100%;
  min-height: 100%; }

h1 {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  background: white;
  background: linear-gradient(to bottom, white 0%, #e5e5e5 100%); }

input {
  outline: none; }

div[id="video-dashboard"] {
  padding: 10px;
  padding-left: 3em;
  margin: 10px;
  width: 375px;
  list-style-type: none;
  display: inline-grid;
  position: relative;
  background-color: white;
  font-family: sans-serif;
  font-size: 15px; }
  @media screen and (min-width: 700px) {
    div[id="video-dashboard"] {
      flex-basis: calc(50% - 2em - 4px); } }
  @media screen and (min-width: 1100px) {
    div[id="video-dashboard"] {
      flex-basis: calc(33.33333% - 2em - 4px); } }
  @media screen and (min-width: 1600px) {
    div[id="video-dashboard"] {
      flex-basis: calc(25% - 2em - 4px); } }
  @media screen and (min-width: 2000px) {
    div[id="video-dashboard"] {
      flex-basis: calc(16.66666% - 2em - 4px); } }
  div[id="video-dashboard"] div[id="clockstart"] span {
    padding-left: 1em; }

body {
  font-family: Arial, Verdana, sans-serif;
  color: #111111; }

table {
  width: 600px; }

th, td {
  padding: 7px 10px 10px 10px;
  line-height: 17px; }

th {
  background-color: cornflowerblue;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-size: 90%;
  border-bottom: 2px solid #111111;
  border-top: 1px solid #999;
  text-align: left; }

tr.even {
  background-color: ghostwhite; }

tr.odd {
  background-color: lightgray; }

tr.hover {
  background-color: #c3e6e5; }

.lineitem {
  text-align: center;
  line-height: 10px;
  color: darkblue;
  font-weight: bold; }
